:root {
    --base-color: #61DAFB;
}

.navbar {
    background-color: rgba(0, 0, 0, 0.15) !important;
    border-bottom: 1px solid rgba(255, 255, 255, 0.15);
    transition: all .2s ease-in-out;
    padding-top: 0;
    padding-bottom: 0;
}

#navbar-header {
    font-size: 2.5rem !important;
    transition: all .2s ease-in-out;
}

#navbar-header span {
    color: var(--base-color);
}

.navbar .navbar-nav .nav-link {
    letter-spacing: 1px;
    color: #fff !important;
    font-size: 0.888rem;
    position: relative;
    text-transform: uppercase;
}

.navbar .navbar-nav .nav-link:before {
    content: '';
    position: absolute;
    left: 0;
    bottom: 5px;
    width: 100%;
    transform: scaleX(0);
    height: 1px;
    background-color: #ffffff;
    transition: all .3s;
}

.navbar .navbar-nav .nav-link:hover:before {
    transform: scaleX(1);
}

.navbar .navbar-nav .nav-link.active {
    color: var(--base-color) !important;
}

.navbar .navbar-nav .nav-link.active:before {
    content: '';
    position: absolute;
    left: 0;
    bottom: 5px;
    width: 100%;
    height: 1px;
    background-color: var(--base-color);
}

.navbar .navbar-nav .nav-item:not(:first-child) {
    margin-left: 1.2rem;
}

#___ytsubscribe_0 {
    margin: .4rem 0 0 0 !important;
}

.navbar.scrolled {
    background-color: #000 !important;
    -webkit-box-shadow: 0px 8px 17px -8px rgba(0, 0, 0, 0.1);
    -moz-box-shadow: 0px 8px 17px -8px rgba(0, 0, 0, 0.1);
    box-shadow: 0px 8px 17px -8px rgba(0, 0, 0, 0.1);
}

.navbar.scrolled #navbar-header{
    font-size: 2.1rem !important;
}

@media screen and (max-width: 768px) {

    .navbar .navbar-nav .nav-item:not(:first-child) {
        margin-left: 0rem;
    }
    
    #___ytsubscribe_0 {
        margin: 0.5rem 0 0 0 !important;
    }

    .navbar {
        background-color: #000 !important;
    }

    .navbar.scrolled #navbar-header {
        font-size: 2.5rem !important;
    }

}