#home-bg-parallax .bg-parallax {
    background-image: url('https://images.unsplash.com/photo-1537884944318-390069bb8665?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1350&q=80');
    background-position: center center !important;
    background-repeat: no-repeat !important;
    background-attachment: fixed;
    height: 100vh;
    width: 100%;
    overflow: hidden;
    background-size: cover;
}

.bg-overlay-black-8 {
    position: relative;
}

.bg-overlay-black-8:before {
    content: '';
    position: absolute;
    display: block;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    background:rgba(0, 0, 0, 0.8) !important;
}

#contact a {
    font-size: 3rem;
    color: #61DAFB;
}